"use client";

import { useState, useRef, useEffect } from "react";
import { Avatar } from "@/components/ui/avatar";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { ThumbsUp, ThumbsDown, RefreshCw, Copy, Plus, Send, Star, Settings } from "lucide-react";
import TypeWriter from "@/components/TypeWriter";
import WaitingIndicator from "@/components/WaitingIndicator";
import Toast from "@/components/Toast";
import { demoResponses } from "@/lib/demoResponses";
import "./typingCursor.css";

interface Message {
  id: string;
  content: string;
  isUser: boolean;
  timestamp: string;
  isStreaming?: boolean;
}

export default function Home() {
  const [messages, setMessages] = useState<Message[]>([
    {
      id: "1",
      content: "Hello! How can I assist you today?",
      isUser: false,
      timestamp: "16:58",
    },
  ]);
  const [responseIndex, setResponseIndex] = useState(0);
  const [inputValue, setInputValue] = useState("");
  const [isTyping, setIsTyping] = useState(false);
  const [isAiResponding, setIsAiResponding] = useState(false);
  const [showToast, setShowToast] = useState(false);
  const [consecutiveMessages, setConsecutiveMessages] = useState(0);
  const chatContainerRef = useRef<HTMLDivElement>(null);

  // Auto-scroll to bottom when messages change
  useEffect(() => {
    if (chatContainerRef.current) {
      chatContainerRef.current.scrollTop = chatContainerRef.current.scrollHeight;
    }
  }, [messages]);

  const handleSendMessage = () => {
    if (!inputValue.trim() || isAiResponding) return;

    // Increment consecutive message counter
    const newConsecutiveCount = consecutiveMessages + 1;
    setConsecutiveMessages(newConsecutiveCount);

    // If user tries to send too many messages without waiting for responses
    if (newConsecutiveCount > 3) {
      setShowToast(true);
      return; // Don't send the message
    }

    // Create a new user message
    const newMessage: Message = {
      id: Date.now().toString(),
      content: inputValue,
      isUser: true,
      timestamp: new Date().toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' }),
    };

    setMessages((prev) => [...prev, newMessage]);
    setInputValue("");
    setIsTyping(true);
    setIsAiResponding(true); // Mark that AI is responding

    // Simulate AI response after a delay
    setTimeout(() => {
      // Get the next demo response or loop back to the beginning
      const responseContent = demoResponses[responseIndex % demoResponses.length];
      setResponseIndex(prevIndex => prevIndex + 1);
      
      const aiResponse: Message = {
        id: (Date.now() + 1).toString(),
        content: responseContent,
        isUser: false,
        timestamp: new Date().toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' }),
        isStreaming: true, // Mark as streaming initially
      };

      setMessages((prev) => [...prev, aiResponse]);
      setIsTyping(false);
      
      // We keep the streaming state active until the typewriter effect completes
    }, 1500);
  };

  return (
    <div className="flex flex-col h-screen bg-background relative">
      <WaitingIndicator isAiResponding={isAiResponding} />
      {/* Admin button */}
      <header className="p-4 flex items-center border-b border-border">
        <div className="flex space-x-2">
          <button className="p-2 hover:bg-secondary rounded-full">
            <Plus className="h-5 w-5" />
          </button>
          <button className="p-2 hover:bg-secondary rounded-full">
            <Star className="h-5 w-5" />
          </button>
        </div>
        <button className="ml-auto p-2 hover:bg-secondary rounded-full">
          <Settings className="h-5 w-5" />
        </button>
      </header>

      {/* Chat container */}
      <div
        ref={chatContainerRef}
        className="flex-1 overflow-auto p-4 space-y-6"
      >
        {messages.map((message) => (
          <div key={message.id} className={`flex ${message.isUser ? 'justify-end' : 'justify-start'}`}>
            <div className={`${message.isUser ? 'chat-message-user' : 'chat-message-ai'}`}>
              {!message.isUser && (
                <div className="flex-shrink-0">
                  <Avatar className="h-8 w-8 bg-primary text-primary-foreground" />
                </div>
              )}
              <div className="flex flex-col">
                <div className={`text-sm ${!message.isUser && message.isStreaming ? 'streaming-cursor' : ''}`}>
                  {!message.isUser && message.isStreaming ? (
                    <TypeWriter 
                      text={message.content} 
                      onComplete={() => {
                        setMessages(prevMessages => 
                          prevMessages.map(msg => 
                            msg.id === message.id 
                              ? { ...msg, isStreaming: false } 
                              : msg
                          )
                        );
                        setIsAiResponding(false); // AI has finished responding
                        setShowToast(true); // Show toast notification
                        setConsecutiveMessages(0); // Reset consecutive messages counter
                      }} 
                    />
                  ) : (
                    message.content
                  )}
                </div>
               </div>
             </div>
           </div>
         ))}

         {isTyping && (
           <div className="flex justify-start">
            <div className="chat-message-ai items-center">
               <Avatar className="h-8 w-8 bg-primary text-primary-foreground">
              </Avatar>
               <div className="flex items-center space-x-1">
                 <div className="h-2 w-2 bg-foreground/70 rounded-full animate-pulse"></div>
                 <div className="h-2 w-2 bg-foreground/70 rounded-full animate-pulse delay-150"></div>
                 <div className="h-2 w-2 bg-foreground/70 rounded-full animate-pulse delay-300"></div>
               </div>
            </div>
          </div>
        )}
      </div>

      {/* Input area */}
      <div className="p-4 border-t border-border">
         <div className="flex items-center space-x-2 max-w-3xl mx-auto">
           <Input
            value={inputValue}
            onChange={(e) => setInputValue(e.target.value)}
            onKeyDown={(e) => e.key === 'Enter' && !isAiResponding && handleSendMessage()}
            placeholder={isAiResponding ? "Waiting for response..." : "Ask a question..."}
            disabled={isAiResponding}
            className={`chat-input ${isAiResponding ? 'opacity-70' : ''}`}
          />
          <Button
            onClick={handleSendMessage}
            disabled={!inputValue.trim() || isAiResponding}
            className="rounded-full p-2 h-10 w-10 flex items-center justify-center"
            variant={inputValue.trim() && !isAiResponding ? "default" : "secondary"}
          >
            <Send className="h-5 w-5" />
          </Button>
        </div>
      </div>
    </div>
  );
}
