"use client";

import { AlertCircle } from "lucide-react";

interface WaitingIndicatorProps {
  isAiResponding: boolean;
}

export default function WaitingIndicator({ isAiResponding }: WaitingIndicatorProps) {
  if (!isAiResponding) return null;
  
  return (
    <div className="absolute bottom-20 left-1/2 transform -translate-x-1/2 bg-secondary text-secondary-foreground px-4 py-2 rounded-full flex items-center gap-2 text-sm animate-pulse shadow-md z-10">
      <AlertCircle className="h-4 w-4" />
      <span>Attendez la réponse complète...</span>
    </div>
  );
}
